body{
  height: 100%;
  width: 100%;
  max-width: 100vw;
  max-height: 100vh;
  font-family: 'Trebuchet MS', sans-serif;
  padding: 0;
  margin: 0;
  overflow: hidden;
  position: absolute;
}

.App{
  width: 100vw;
  height: 100vh;
  overflow: hidden;
  position: absolute;
}

.Card {
  border-radius: 1.5em;
  border: solid white 2px;
  height: 25em;
  width: 15.7em;
  text-align: center;
  position: absolute;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  transition: 1s;

}

.Left-T{
  float: left;
  background: linear-gradient(-90deg, black 50%, rgb(0, 50, 0) 100%);
}

.Right-D{
  text-align: end;
  float: right;
  background: linear-gradient(90deg, black 50%, rgb(50, 0, 0) 100%);
}

h1 {
  color: white;
  height: 100vh;
  width: 50vw;
  font-size: xxx-large;
}

.H1L{
  margin-left: 1em;
  margin-right: -1em;
}

.H1R {
  margin-left: -1em;
  margin-right: 1em;
}

.Truth{
  border-color: gold;
  background-color: white;
  z-index: 10;
}
.Dare{
  background-color: Black;
  color: White;
}

.Welcome{
  background-color: white;
}

.Button-cont{
  position: absolute;
  background-color: black;
  left: 50%;
  transform: translate(-50%, -0%);
  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px;
  overflow: hidden;
  top: -2px;
  border: 2px white solid;
}

button{
  height: 2.5em;
  background: black;
  color: white;
  border: none;
}

.active1{
  background-color: rgb(51, 183, 51);
  border-top-right-radius: 10px;
  border-bottom-right-radius: 10px;
}

.active2{
  background-color: rgb(255, 56, 56);
  border-radius: 10px;
}

.active3{
  background-color: blueviolet;
  border-top-left-radius: 10px;
  border-bottom-left-radius: 10px;
}